.banner {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 95vh;
  gap: 50px;
}

.banner .title {
  font-family: var(--primary-font);
  font-size: 50px;
  text-align: center;
  border-bottom: 3px solid var(--tertiary-color);
}

.banner .perfil-links {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 100px;
}

.banner .perfil {
  width: 40%;
  border-radius: 50%;
  border: 3px solid var(--tertiary-color);
  padding: 3px;
}

.banner .links {
  width: 40%;
  display: flex;
  flex-direction: column;
  font-size: 30px;
  text-align: center;
  gap: 10px;
}

.banner i {
  padding: 0 10px;
}

.banner a {
  text-decoration: none;
  color: var(--tertiary-color);
  border: 1px solid var(--tertiary-color);
  border-radius: 10px;
  padding: 10px;
  background-color: var(--light-hover-color);
  font-weight: 500;
}

.banner a:hover {
  background-color: var(--secundary-color);
}

.banner .description {
  font-family: var(--primary-font);
  font-size: 25px;
  text-align: center;
  max-width: 800px;
}

@media screen and (width <= 1500px) {
  .banner {
    margin-top: 50px;
  }
}

@media screen and (width <= 1150px) {
  .banner {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 25px;
  }

  .banner .title {
    font-size: 20px;
  }

  .banner .perfil-links {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 25px;
  }

  .banner .perfil {
    width: 30%;
    border-radius: 50%;
    border: 3px solid var(--tertiary-color);
    padding: 3px;
  }

  .banner .links {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    font-size: 20px;
    text-align: center;
    justify-content: center;
    gap: 10px;
  }

  .banner i {
    padding: 0 10px 0 0;
  }

  .banner a {
    text-decoration: none;
    color: var(--tertiary-color);
    border: 1px solid var(--tertiary-color);
    border-radius: 10px;
    padding: 10px;
    background-color: var(--light-hover-color);
    font-weight: 500;
  }

  .banner .description {
    font-size: 20px;
    padding: 0 20px;
  }
}
