header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}

header nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: var(--secundary-color);
  padding: 5px 100px;
  gap: 5px;
}

header nav a {
  color: var(--font-color);
  text-decoration: none;
  text-align: center;
  padding: 10px;
  border-radius: 10px;
  white-space: nowrap;
  font-size: 18px;
  font-weight: 600;
}

header nav a:hover {
  background-color: var(--dark-hover-color);
}

header nav .options {
  display: inherit;
}

header nav .menu {
  display: none;
  color: var(--font-color);
  text-align: center;
  padding: 10px;
  border-radius: 10px;
  font-size: 18px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

header nav .menu:hover {
  background-color: var(--dark-hover-color);
}

.nav-dropdown {
  display: none;
  justify-content: end;
  background-color: transparent;
  padding: 0;
}

.nav-dropdown.open {
  display: flex;
}

header nav .options-dropdown {
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 10px 20px;
  background-color: var(--secundary-color);
  gap: 10px;
}

@media screen and (width <= 1500px) {
  header nav .options {
    width: 100%;
    justify-content: center;
  }
}

@media screen and (width <= 1150px) {
  /* Header */
  header nav {
    padding: 5px 10px;
  }

  header nav .options {
    display: none;
  }

  header nav .menu {
    display: block;
  }
}

.language-switcher {
  display: flex;
  align-items: center;
  overflow: hidden;
  border: 2px solid var(--tertiary-color);
  border-radius: 10px;
}

.language-switcher button {
  background-color: transparent;
  color: var(--font-color);
  padding: 15px 12px;
  cursor: pointer;
  font-weight: 600;
  font-size: 16px;
  border: none;
  box-shadow: none;
}

.language-switcher button:hover {
  background-color: var(--dark-hover-color);
}

.language-switcher button.active {
  background-color: var(--tertiary-color);
  color: var(--primary-color);
}
